(function(){
  angular.module('shortcodeApp', [])
    .controller('shortcodeController', ['$scope',
      function ($scope) {
        var shortcodeName;

        $scope.form = {
          selectCheckbox: false,
          selectedLocations: '',
          loading: true,
          locations: [],
          viewType: '',
          title: '',
          shortcode: '',
          viewTypes: [
            {name: 'Front Page', value: 'front'},
            {name: 'Location Page', value: 'location'},
            {name: 'All Locations Page', value: 'all'},
            {name: 'Climbing Page', value: 'climbing'}
          ]
        };

        $scope.init = function (d) {
          var data = angular.fromJson(d);
          shortcodeName = data.shortcodeName;
          $scope.form.locations = data.hours;
          $scope.form.loading = false;
        };

        $scope.createShortcode = function () {
          var idsArray = [];
          angular.forEach($scope.form.selectedLocations, function (id) {
            idsArray.push(id);
          });

          $scope.form.shortcode = '';
          if($scope.form.viewType == 'all'){
            $scope.form.shortcode = '[' + shortcodeName + ' locations="all"]';
          } else if($scope.form.viewType == 'location'){
            var title = $scope.form.title ? $scope.form.title : 'Weekly Hours';
            $scope.form.shortcode = '[' + shortcodeName + ' locations="' + idsArray + '" view="location" title="' + title + '"]';
          } else if (idsArray.length > 0 && $scope.form.viewType !== '') {
            $scope.form.shortcode = '[' + shortcodeName + ' locations="' + idsArray + '" view="' + $scope.form.viewType + '"]';
          }
        };
      }])
    .directive('selectOnClick', function () {
      return {
        restrict: 'A',
        link: function (scope, elem) {
          elem.on('click', function () {
            this.select();
          });
        }
      };
    });

}());